  .Resume .box::after {
    content: "";
    position: absolute;
    top: 13%;
    left: -9%;
    height: 5px;
    width: 30px;
  }
}
/* ==========IPAD - MINI- ============= */

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
